
const ModalInfo = ({ modalInfo }) => {
    return(
        modalInfo.map((item) => {
            const {title, info, id} = item
            return(
                <div className="flex flex-col gap-0">
                    <h3 key={id} className="font-bold text-2xl text-white">{title}</h3>
                    <p className="text-gray-400 text-sm xl:text-base">{info}</p>
                </div>
            )
        })
    )
}

export default ModalInfo;