import { useAccount, useDisconnect } from 'wagmi';

import { ReactComponent as Logo } from './../../assets/svgs/logo.svg';
import { ReactComponent as InfoLogo } from '../../assets/svgs/info.svg';

const Navigation = () => {
    const { address, isConnected } = useAccount();
    const { disconnect } = useDisconnect();

  const shorthand = (str) => {
    return str.slice(0, 6) + '...' + str.slice(str.length - 4, str.length);
  };

  return (
    <div className="navigation p-5 border-b border-line-rule sticky top-0 left-0 right-0 backdrop-blur-2xl z-max">
      <div className="flex justify-between items-center">
        <Logo className="w-[50%]  max-w-[100px]" alt="Save the Stoics Logo" />

        <div className="flex items-center gap-0 2xl:gap-6 ">
          <div className="">
            {!isConnected ? (
              <div
                className="whitespace-nowrap flex  items-center text-yellow-700 px-2 gap-1 p-1.5 bg-yellow-100 rounded-full dark:bg-yellow-200 dark:text-yellow-800"
                role="alert"
              >
                <InfoLogo className="w-5 2xl:w-7 2xl:h-7 h-5" />
                <span className="sr-only">Info</span>
                <p className="text-xs xxs:text-sm 2xl:text-xl">Wallet not connected</p>
              </div>
            ) : (
              <div className="flex items-center">
                <div
                  className="whitespace-nowrap flex  items-center text-yellow-700 px-2 gap-1 p-1.5 bg-yellow-100 rounded-full dark:bg-yellow-200 dark:text-yellow-800 mr-2"
                  role="alert"
                >
                  <p className="text-xs xxs:text-sm 2xl:text-xl">{shorthand(address)}</p>
                </div>
                <button className="btn-primary" onClick={disconnect}>
                  <div className="py-0.5 2xl:py-2 text-base items-center  text-[1rem] 2xl:text-3xl justify-center gap-2 flex text-center">
                    <span> Disconnect </span>
                  </div>
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Navigation;
