import { Link } from "react-router-dom";

const ModalTabs = ({tabs}) => {
    return(
        <ul className="flex flex-wrap text-sm font-medium text-center text-gray-500 border-b border-line-rule dark:border-gray-700 dark:text-gray-400 gap-1">
            {tabs.map((item) => {
                const {tab, id, link} =  item;
                return(
                    <li key={id} className="">
                        <Link to={link} key={id} className="inline-block xs:p-2.5 xs:px-8 px-4 p-2.5 hover:border-btn-clr hover:text-btn-hvr hover:transition-[350ms] border-line-rule border rounded-t-xl hover:bg-btn-bg bg-card-bg xl:text-base 2xl:text-2xl">{tab}</Link>
                    </li>
                )
            })}
        </ul>
    )
}

export default ModalTabs;