/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef, useState } from 'react';
import { useWeb3Modal } from '@web3modal/react';
import { useAccount, useContractReads, useSigner, useContract } from 'wagmi';
import axios from 'axios';
import { toast } from 'react-toastify';
import { utils as etherUtils } from 'ethers';

import Etherium from './../../../assets/images/etherium.png';
import NftImage from './../../../assets/nfts/nft2.png';
import { ReactComponent as EtheriumLogo } from './../../../assets/svgs/etherium.svg';

import './../../../slider.scss';
import './../../../App';

import ContractABI from '../../../contracts/ABI.json';
import { getContractsData, formatData } from '../../../helpers/common';

const contractDetails = {
  address: process.env.REACT_APP_CONTRACT_ADDRESS,
  abi: ContractABI,
};

const MintCard = () => {
  const { open } = useWeb3Modal();
  const { address, isConnected } = useAccount();
  const { data: signer } = useSigner({
    onError(error) {
      toast.error(error.message);
    },
  });
  const {
    data: contractData,
    error,
    isLoading: isFetching,
  } = useContractReads({
    scopeKey: 'emon',
    contracts: getContractsData(contractDetails),
    cacheOnBlock: true,
    watch: true,
    select: (data) => formatData(data),
  });
  console.log(' ~ file: mint-card.component.jsx:43 ~ MintCard ~ contractData', contractData);
  const contract = useContract({
    ...contractDetails,
    signerOrProvider: signer,
  });

  const tooltip = useRef([]);
  const [fetching, setFetching] = useState(false);
  const [proof, setProof] = useState([]);
  const [count, setCount] = useState(1);

  const getWhitelistedUsers = async () => {
    try {
      setFetching(true);
      const response = await axios.get(process.env.NEXT_PUBLIC_WHITELIST_API);
      const res = await axios.post(
        `${process.env.NEXT_PUBLIC_MERKEL_TREE_API}/hash`,
        {
          data: { whitelist: JSON.stringify(response.data.data), address },
        },
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      );
      const { hash, proof } = res.data;
      console.log(' ~ file: index.js ~ line 114 ~ getWhitelistedUsers ~ hash', hash);
      setProof(proof);
      setFetching(false);
    } catch (error) {
      console.log(error);
      setFetching(false);
    }
  };

  useEffect(() => {
    getWhitelistedUsers();
  }, []);

  useEffect(() => {
    if (error) {
      toast.error(error.message);
    }
  }, [error]);

  const mint = async () => {
    if (contract && address) {
      try {
        toast.info('Please wait while we mint your NFT');
        const value = etherUtils.parseEther(
          (Number(contractData.cost).toFixed(2) * count).toString()
        );
        const estimate = await contract.estimateGas.mint(1, {
          value,
          from: address,
        });
        const trx = await contract.mint(count, {
          value,
          from: address,
          gasLimit: estimate,
        });
        const receipt = await trx.wait();
        console.log(receipt);
        toast.success('Woohoo! NFT minted successfully!');
      } catch (error) {
        toast.error(error.reason || error.message);
      }
    }
  };

  const whitelistMint = async () => {
    if (contract && address) {
      try {
        toast.info('Please wait while we mint your NFT');
        const value = etherUtils.parseEther(
          (Number(contractData.cost).toFixed(2) * count).toString()
        );
        const estimate = await contract.estimateGas.whitelistMint(count, proof, {
          value,
          from: address,
        });
        const trx = await contract.whitelistMint(count, proof, {
          value,
          from: address,
          gasLimit: estimate,
        });
        const receipt = await trx.wait();
        console.log(receipt);
        toast.success('Woohoo! NFT minted successfully!');
      } catch (error) {
        toast.error(error.reason || error.message);
      }
    }
  };
    
  const freeMint = async () => {
    if (contract && address) {
      try {
        toast.info('Please wait while we mint your NFT');
        const value = etherUtils.parseEther(
          (Number(contractData.cost).toFixed(2) * 1).toString()
        );
        const estimate = await contract.estimateGas.whitelistMint(1, proof, {
          value,
          from: address,
        });
        const trx = await contract.whitelistMint(1, proof, {
          value,
          from: address,
          gasLimit: estimate,
        });
        const receipt = await trx.wait();
        console.log(receipt);
        toast.success('Woohoo! NFT minted successfully!');
      } catch (error) {
        toast.error(error.reason || error.message);
      }
    }
  };

  let varPercent;

  function fillWidth() {
    const slider = document.querySelector('.slider-container .bar .fill');
    slider.style.width = varPercent;
  }

  const handleChange = (e) => {
    // N.B for the fill to work properly, the input must have a max value of 9 with no min value. - that means the e.target.value is one behind the actual value we want (10) as it caps at 9 but starts at 0 (we dont want it to start at 0 or end at 9 so we increment the value by 1 before passing it on to the state)
    let x = e.target.value;
    x++;
    setCount(x);

    varPercent = e.target.value * 11 + '%';
    fillWidth();
  };

  const cardData = [
    {
      name: 'NFT(s)',
      price: contractData?.cost || 0.1,
      collection: 'Save the Stoics',
      value: count,
      minted: contractData?.totalSupply || 0,
      available: contractData?.maxSupply || 4444,
      id: 1,
    },
  ];

  return cardData.map((item) => {
    const { name, id, price, collection, value, minted, available } = item;

    return (
      <div
        key={id}
        className="max-w-[800px] 2xl:max-w-[1200px] flex flex-col gap-5 mx-auto py-20 md:px-10 px-5 overflow-x-hidden"
      >
        <div
          id="card-image"
          className="flex flex-col relative overflow-hidden -z-10 2xl:min-w-[800px] md:min-w-[550px] backdrop-blur-2xl base:mx-0 sm:mx-10 items-center justify-center border-line-rule border rounded-[32px] sm:aspect-[2/1] md:mb-10 mb-5"
        >
          <img src={NftImage} className=" max-h-[1000px]" alt="Save the Stoics NFT Mint Preview" />
        </div>

        <div
          id="card-header"
          className="flex flex-row items-center justify-between 2xl:py-6 gap-1.5 md:pb-2 pb-4 border-b border-b-line-rule"
        >
          <h3 className="text-2xl 2xl:text-5xl font-bold uppercase text-white">{collection}</h3>

          <div className="">
            <div
              id="etherium"
              className="xxs:visible hidden invisible xxs:block max-w-[30px] 2xl:max-w-[40px] aspect-square relative"
            >
              <img src={Etherium} alt="etherium logo" />
              <div
                id="tooltip-bottom"
                ref={tooltip}
                role="tooltip"
                className="inline-block 2xl:text-xl invisible opacity-0 left-0 -bottom-10 2xl:-bottom-3 2xl:translate-y-full -translate-x-1/2 absolute z-max py-2 px-3 text-xs font-medium text-white rounded-lg shadow-sm whitespace-nowrap tooltip bg-card-bg"
              >
                Ethereum Chain
                <div className="tooltip-arrow" data-popper-arrow></div>
              </div>
            </div>
          </div>
        </div>

        <div id="card-footer" className="flex justify-between items-start">
          <div className="flex gap-0.5 items-center ">
            <EtheriumLogo className="w-4 h-4 2xl:w-6 2xl:h-6" />
            <p className="text-white">{`${price}` + ` ` + `ETH`} </p>
          </div>

          <div className="flex flex-col items-end">
            <p className="uppercase textGradient">Minted</p>
            <p className="text-white">{`${minted}` + `/` + `${available}`}</p>
          </div>
        </div>

        <div className="w-full flex flex-col gap-2">
          <p className="text-xs xl:text-xl text-gray-300">
            {value} {name}
          </p>
          <div className="middle">
            <div className="slider-container">
              <span className="bar">
                <span className="fill"></span>
              </span>
              <input
                id="slider"
                name="range1"
                type="range"
                max={Number(contractData?.maxMintAmountPerTx || 10) - 1}
                min="0"
                onChange={handleChange}
                className="slider"
                defaultValue={0}
              />
            </div>
          </div>
        </div>

        <button
          className="btn-primary"
          onClick={
            !isConnected
              ? open
              : contractData?.isFreesaleActive
              ? freeMint
              : contractData?.isPresaleActive
              ? whitelistMint
              : contractData?.isSaleActive
              ? mint
              : contractData?.isFreesaleActive
              ? mint
              : null
          }
          disabled={
            isFetching ||
            fetching ||
            !contractData?.isPresaleActive ||
            !contractData?.isFreesaleActive ||
            !contractData?.isSaleActive
          }
        >
          <div className="py-0.5 2xl:py-2 text-base items-center  text-[1rem] 2xl:text-3xl justify-center gap-2 flex text-center">
            {!isConnected && <i className="bx bx-wallet"></i>}
            <span>
              {' '}
              {!isConnected
                ? 'Connect Wallet'
                : isFetching || fetching
                ? 'Please wait...'
                : contractData?.totalSupply === contractData?.maxSupply
                ? 'SOLD OUT'
                : !contractData?.isPresaleActive ||
                  !contractData?.isFreeSaleActive ||
                  !contractData?.isSaleActive
                ? 'Sale Not Active'
                : 'Mint'}{' '}
            </span>
          </div>
        </button>
      </div>
    );
  });
};

export default MintCard;
