import { useEffect  } from "react"
import { useRef } from "react"
import gsap from "gsap"
// import SplitText from 'gsap/SplitText'
// gsap.registerPlugin(gsap, SplitText)


const questions = [
    {
        title: 'How do I Mint?',
        answer: ' Lorem ipsum dolor sit amet consectetur adipisicing elit. Nobis modi qui non ullam hic maxime veritatis et nemo eius nihil eos rem, asperiores nulla aspernatur quisquam eaque officiis atque odit?',
        id: 1,
    },
    {
        title: 'How many Save the Stoics Can I Mint?',
        answer: ' Lorem ipsum dolor sit amet consectetur adipisicing elit. Nobis modi qui non ullam hic maxime veritatis et nemo eius nihil eos rem, asperiores nulla aspernatur quisquam eaque officiis atque odit?',
        id: 2,
    },
    {
        title: 'Where can I find my NFT?',
        answer: ' Lorem ipsum dolor sit amet consectetur adipisicing elit. Nobis modi qui non ullam hic maxime veritatis et nemo eius nihil eos rem, asperiores nulla aspernatur quisquam eaque officiis atque odit?',
        id: 3,
    },
    {
        title: 'How do I join the premium Discord after minting?',
        answer: ' Lorem ipsum dolor sit amet consectetur adipisicing elit. Nobis modi qui non ullam hic maxime veritatis et nemo eius nihil eos rem, asperiores nulla aspernatur quisquam eaque officiis atque odit?',
        id: 4,
    },
]



const Faq = () => {
    const app = useRef([])

    useEffect(() => {
        let ctx = gsap.context(() => {
            let activeEl
            let faq = gsap.utils.toArray('.question');

            faq.forEach((element) => {
                let answer = element.querySelector('.answer');
                let isActive;
                let animation = gsap.timeline()
                .to(answer, {
                    autoAlpha: 0,
                    duration: 0.05
                })
                .to(answer, {
                    height: 0,
                    duration: 0.4,
                    ease: 'power3.inOut'
                }, "<");
              animation.progress(1);
              
              element._open = (active) => {
                isActive = active;
                if (active) {
                  if (activeEl !== element && activeEl) {
                    activeEl._open(false);
                  }
                  animation.reverse();
                  activeEl = element;
                } else {
                  animation.play();
                  if (activeEl === element) {
                    activeEl = null;
                  }
                }
              }
              element._toggle = () => element._open(!isActive);
              element.addEventListener('click', element._toggle);
            });
          // cleanup event listeners
          return () => faq.forEach(element => element.removeEventListener("click", element._toggle));
        });
      
      // cleanup context
      return () => ctx.revert();
    }, []);


    return(
    questions.map(item => {
        const {title,id, answer} = item;
        
        return (
        <div className="question px-5 md:px-10 max-w-[1200px] 2xl:max-w-[1600px] mx-auto"  key={id} ref={(index, element) => app.current.splice(index, 1, element).push(element)}>
            <div key={id}  className='md:mb-5 mb-3 rounded-2xl border text-start text-gray-300 border-line-rule  hover:border-btn-clr hover:text-btn-hvr hover:transition-[350ms] hover:bg-btn-bg 2xl:p-6 p-3 md:p-4 flex flex-col items-start justify-start'>
            <button className='closeBtn  py-1 2xl:text-3xl md:text-[1.125rem] text-start flex justify-between items-start 2xl:py-2.5 w-full'>
                <div className="">
                    <h3 id="" className="border-line-rule text-lg md:text-xl max-w-[25ch] xs:max-w-none xl:text-xl 2xl:text-3xl">{title}</h3>
                </div>

                <div className='close'>
                    <svg data-accordion-icon className="w-6 h-6 2xl:w-8 2xl:h-8 shrink-0" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path  d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" ></path></svg>
                </div>
            </button>

            <div id="answer" className='answer'>
                <p className='text-gray-400 text-base xxs:text-base xl:text-base 2xl:text-2xl'>
                    {answer}
                </p>
            </div>
        </div>
    </div>)
    })
    )
}

export default Faq