import { EthereumClient, modalConnectors, walletConnectProvider } from '@web3modal/ethereum';
import { Web3Modal } from '@web3modal/react';
import { configureChains, createClient, WagmiConfig } from 'wagmi';
import { mainnet } from 'wagmi/chains';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import './App.css';
import Faq from './components/faq/faq.component';
import Footer from './components/footer/footer.component';
import Navigation from './components/header/header.component';
import MintCard from './components/mint-card/card/mint-card.component';
import CryptoModal from './components/crypto-modal/modal.component.jsx';

const { REACT_APP_PROJECT_ID } = process.env;

const chains = [mainnet];
const projectId = REACT_APP_PROJECT_ID;
// Wagmi client
const { provider } = configureChains(chains, [
  walletConnectProvider({ projectId: projectId }),
]);
const wagmiClient = createClient({
  autoConnect: true,
  connectors: modalConnectors({ appName: 'web3Modal', chains }),
  provider,
});

// Web3Modal Ethereum Client
const ethereumClient = new EthereumClient(wagmiClient, chains);

function App() {
  return (
    <>
      <WagmiConfig client={wagmiClient}>
        <div className="relative w-full ">
          <Navigation />
          <div id="content" className="md:pb-40 pb-20 ">
            <MintCard />
            <Faq />
          </div>

          <div
            id="parent-modal"
            className="md:fixed z-[-1000000] md:bottom-1/2 md:left-1/2 md:-translate-x-1/2 md:translate-y-1/2"
          >
            <div className="sticky md:static bottom-0 left-0 right-0">
              <div className="relative  h-full w-full">
                <div
                  id="modal"
                  className="invisible md:static fixed w-full left-0 bottom-0 right-0"
                >
                  <CryptoModal />
                </div>
              </div>
            </div>
          </div>
          <Footer />
          <ToastContainer
            position="top-left"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss={false}
            draggable
            pauseOnHover={false}
            theme="dark"
          />
        </div>
      </WagmiConfig>
       <Web3Modal
        projectId={projectId}
        ethereumClient={ethereumClient}
      />
    </>
  );
}

export default App;
