import { utils } from 'ethers';

const data = [
  'cost',
  'maxMintAmountPerTx',
  'maxSupply',
  'totalSupply',
  'isFreesaleActive',
  'isPresaleActive',
  'isSaleActive',
];

export const getContractsData = (contractData) => {
  const res = data.map((d) => ({
    ...contractData,
    functionName: d,
  }));

  return res;
};

export const formatData = (contractData) => {
  const formattedData = data.reduce((acc, curr, i) => {
    return {
      ...acc,
      [curr]:
        i <= 3
          ? i === 0
            ? utils.formatEther(contractData[i])
            : Number(contractData[i]._hex)
          : contractData[i],
    };
  }, {});

  return formattedData;
};

export const formatNumbers = (num) => {
  return Number(num).toLocaleString('en');
};
