import ModalTabs from './modal-elements/modal-tabs.component'
import ModalTab2 from './routes/tab-2/modal-tab2.component'
import ModalTab3 from './routes/tab-3/modal-tab3.component'
import ModalTab1 from './routes/tab-1/modal-tab1.component'
import dayjs from 'dayjs'
import { Outlet, Routes, Route } from 'react-router-dom'
import timezone from 'dayjs/plugin/timezone'
import utc from 'dayjs/plugin/utc'
import duration from 'dayjs/plugin/duration'
import { useState } from 'react'
import Metamask from './../../assets/svgs/metamask.svg'
import CoinbaseLogo from './../../assets/svgs/coinbase.svg'
import OperaLogo from './../../assets/svgs/opera.svg'
import WalletConnect from './../../assets/svgs/walletconnect.svg'
import FortMatic from './../../assets/svgs/fortmatic.svg'
import Wallet from './../../assets/svgs/wallet.svg'
import Trust from './../../assets/svgs/trust.svg'
import Other from './../../assets/svgs/more.svg'

const tabs = [
    {
        tab: 'Connect',
        id: '1',
        link: '/connect'
    },
    {
        tab: 'Information',
        id: '2',
        link: '/info'
    },
    {
        tab: 'Socials',
        id: '3',
        link: '/socials'
    },
]



const modalList = [
    {
        link: 'www.google.com',
        svg: Metamask,
        wallet: 'Metamask',
        id: '1'
    },
    {

        link: 'www.google.com',
        svg: Trust,
        wallet: 'Trust Wallet',
        id: '2'

    },
    {
        link: 'www.google.com',
        svg: CoinbaseLogo,
        wallet: 'Coinbase',
        id: '3'

    },
    {
        link: 'www.google.com',
        svg: OperaLogo,
        wallet: 'Opera',
        id: '4'
    },
    {
        link: 'www.google.com',
        svg: WalletConnect,
        wallet: 'Wallet',
        id: '5'
    },
    {
        link: 'www.google.com',
        svg: FortMatic,
        wallet: 'Fortmatic',
        id: '6'
    },
    {
        link: 'www.google.com',
        svg: Wallet,
        wallet: 'Injection',
        id: '7'
    },
    {
        link: 'www.google.com',
        svg: Other,
        wallet: 'Other',
        id: '8'
    },
];


const CryptoModal = () => {

dayjs.extend(utc);
dayjs.extend(timezone);
dayjs.extend(duration);


dayjs.tz.guess()


const [days, setDays] = useState()
const [hours, setHours] = useState()
const [min, setMin] = useState()
const [sec, setSec] = useState()


function activateCountDown(dateString){

    const targetDate = dayjs(dateString);
    setInterval(() => {

        const now = dayjs();
        // Calculating the difference between todays date and the selected date in milliseconds
        const duration = dayjs.duration(targetDate.diff(now));

        if(duration.asMilliseconds() > 0){
            setDays(duration.days().toString().padStart(2, '0'));
            setHours(duration.hours().toString().padStart(2, '0'));
            setMin(duration.minutes().toString().padStart(2, '0'));
            setSec(duration.seconds().toString().padStart(2, '0'));
        }
        
    }, 250);
}

const timeInfo = [
    {
        time: days,
        id: 'Days'
    },
    
    {
        time: hours,
        id: 'Hours',
    },
    
    {
        time: min,
        id: 'Minutes'
    },
    
    {
        time: sec,
        id: 'Seconds'
    }
]

const time = dayjs('2022-11-15T12:00').tz("America/New_York")

activateCountDown(time)

    return(
        <div className="mx-auto md:min-w-[600px] base:min-w-[800px] 2xl:min-w-[1200px]">
            <div className="border-t relative border-line-rule bg-card-bg  rounded-tr-3xl md:rounded-2xl p-5 md:p-10">

                {/* Tabs */}
                <div className="absolute top-0 -translate-y-full md:left-5 left-1">
                    <ModalTabs tabs={tabs} />
                </div>

                <Outlet />
                
                <Routes>
                    <Route index element={<ModalTab1 list = {modalList} />}></Route>
                    <Route path='connect' element={<ModalTab1 list = {modalList} />}></Route>
                    <Route path='info' element={<ModalTab2 time={timeInfo}  />}></Route>
                    <Route path='socials' element={<ModalTab3 />}></Route>
                </Routes>
            </div>
        </div>
    )
}

export default CryptoModal;